      <!-- 페이지 최상단 버튼(replace) -->
      <button @click="toTop()" id="top" class="fixed bottom-[90px] right-10 fd md:right-8 sm:right-2 duration-500"><img src="./assets/images/btn_top.png" alt="상단으로 가기"
