        isBtn == 2 ?  'text-white' : 'text-black']"
        v-if="Trackings.invoiceNo !== ''"  
        @click="PostList()">조회하기</button>
        <!-- trackingDetails - 로컬 / PostList() - 스크립트 변수명(작명한 변수) -->
      </div>
    </div>
    <p class="text-center text-2xl text-red-500 font-bold">{{ errorMsg }}</p>
    <!-- cj 대한통운 -363804378896 -->
    <!-- 한진 택배 - 800358363324 -->

    <!-- 조회 결과 -->
    <div v-if="isShow && errorMsg !== true">
      <div class="w-full  0 pt-5">
        <p class="-mt-4 mb-3 font-[550] text-[24px] text-center tracking-widest">배송조회 결과</p>
        <!-- flex justify-center py-10 px-5 flex-wrap items-start text-center -->
        <div class="flex justify-center py-10 px-5 flex-wrap items-start text-center text-white"
        :class="[
        isBtn == 1 && themeColor[theme] && themeColor[theme].active, 
        // themeColor[theme] && themeColor[theme].hover, 
        isBtn == 1 ?  'text-white' : 'text-black',
        isBtn == 2 && themeColor[theme] && themeColor[theme].active, 
        isBtn == 2 ?  'text-white' : 'text-black']">
          <span class="text-2xl  basis-[45%] font-bold mr-5 mb-5"><span class="border rounded-[14px] px-3 py-[2px] pt-1">운송장 번호</span></span>
          <h3 class="text-2xl basis-[45%] font-bold mb-5">{{ Trackings.invoiceNo }}</h3>
          
          <span class="text-2xl basis-[45%] font-bold mr-5">택배사</span>
          <h3 class="text-2xl basis-[45%] font-bold mb-5">{{ TrackingCode[0] }}</h3>

          <span class="text-2xl basis-[45%] font-bold mr-5 mb-5">발송인 : </span>
          <h3 class="text-2xl basis-[45%] font-bold mb-5" v-if="Trackings.senderName">{{ Trackings.senderName }}</h3>
          <h3 class="text-2xl basis-[45%] font-bold mb-5" v-if="Trackings.senderName == ''">null</h3>

          <span class="text-2xl basis-[45%] font-bold mr-5 mb-5">수취인 : </span>
          <h3 class="text-2xl basis-[45%] font-bold mb-5" v-if="Trackings.receiverName">{{ Trackings.receiverName }}</h3>
          <h3 class="text-2xl basis-[45%] font-bold mb-5" v-if="Trackings.receiverName == ''">null</h3>

          <span class="text-2xl basis-[45%] font-bold mr-5">수취인 주소 : </span>
          <h3 class="text-2xl basis-[45%] font-bold mb-5" v-if="Trackings.receiverAddr">{{ Trackings.receiverAddr }}</h3>
          <h3 class="text-2xl basis-[45%] font-bold mb-5" v-if="Trackings.receiverAddr == ''">null</h3>

          <span class="text-2xl basis-[45%] font-bold mr-5">제품 명 / 분류 : </span>
          <h3 class="text-xl basis-[45%] font-bold mb-5 tracking-widest" v-if="Trackings.itemName">{{ Trackings.itemName }}</h3>
          <h3 class="text-xl basis-[45%] font-bold mb-5" v-if="Trackings.itemName == ''">null</h3>
          <!-- receiverName
          receiverAddr
          itemName -->


          <!-- filter값은 무조건 배열 형태로 반환됨 -->
          <!-- TrackingCode[0] = 첫 번째 값 밖에 없으므로 0번 -->
        </div>
      </div>
      <div class="my-5 flex justify-around">
        <div v-for="level in 5" :key="level">
        <!-- 첫번 째 방식 -->
        <!--v-for="(level,index) in 5"는 index번호를 따라감 -->
        <!-- <img v-if="Trackings.level-1 === level" :src="require(`@/assets/images/ic_sky_delivery_step${level}_on.png`)" />
        <img v-else :src="require(`@/assets/images/ic_sky_delivery_step${level}_off.png`)" >
        <img :src="require(`@/assets/images/ic_sky_delivery_step${level}_off.png`)" /> -->


        <!-- 두번 째 방식 / 삼항 연산자 = 리액트에서는 삼항연산자를 써야함 -->
          <img class="mx-auto" :src="Trackings.level-1 === level 
          ? // require는 데이터 바인딩 시 불러올 때 필요- 경로 설정 
          require(`@/assets/images/ic_sky_delivery_step${level}_on.png`) 
          : // 전자가 참, 후자가 거짓
          require(`@/assets/images/ic_sky_delivery_step${level}_off.png`)">
          <p class="tracking-wider">{{ PostListName[level-1] }}</p>
        </div>
      </div>
      <div class="py-5">
        <ul class="flex justify-around border-b-[3px] text-[20px] ml-11 tracking-wide">
          <li class="">시간</li>
          <li class="">현재 위치 | 상태</li>
          <li class="">전화번호</li>
        </ul>
        <!-- 끌어온 데이터를 역순 정렬 reverse() -->
        <ul v-for="(e, index) in Trackings.trackingDetails.slice().reverse()" :key="index" class="mt-7 flex justify-between tracking-wide last:border-b-[3px] last:pb-6">
          <li class="basis-1/3 text-center">{{ e.timeString }}</li>
          <li class="basis-1/3 text-center">{{ e.where }} | {{ e.kind }}</li>
          <li class="basis-1/3 text-center" v-if="e.telno !== ''">{{ e.telno }}</li>
          <li class="basis-1/3 text-center" v-if="e.telno == ''">Not Registered</li>
        </ul>
      </div> 
    </div>
    <!-- 하단 영역 -->
    <div clss="w-full">
      <div class=" text-center">
        <p class="text-[25px] mb-2 mt-5">다운로드</p>
        <p>구글 플레이와 애플 앱스토어에서 무료로 다운로드하세요.</p>
        <div class="flex justify-center mb-2 mt-2">          
          <a class="mr-10" href="https://play.google.com/store/apps/details?id=com.sweettracker.smartparcel&hl=ko"><img src="./assets/images/btn_google.png" target="_blank" alt="구글 플레이"></a>
          <a href="https://apps.apple.com/kr/app/seumateutaegbae-taegbaejohoe/id523045854"><img src="./assets/images/btn_apple.png" target="_blank" alt="애플 스토어"></a>
        </div>
        <p class="text-[14px] mb-5 text-[lightgrey]">(안드로이드와 iOS의 차이점으로 인해 기능상의 차이가 있음을 알려드립니다.)</p>
      </div>
    </div> 
  </div>   
</template>

<script>

import axios from 'axios'
// import CarrierList from './assets/Company.json'
// import Tracking from './assets/Tracking.json'

// import는 가져오다
// 뒤에 붙는 이름은 작명, 아무거나 작성 가능하지만 한글은 자제
// 어디에서 가져오는가..
// 소스위치 설정
export default {
  name: 'App',

  data() {
    return {
      openbtn: false,
      themebtn: 1,
      isBtn: 1,
      selectIndex: 0,
      t_key: "YohzixCIJ1BU3dAsaesNiA", // t_key - api 개인에게 할당된 키
      t_code: "04", // 택배사 코드
      t_invoice: "",  // 운송장 번호
      Carriers: [],
      Trackings: [],
      PostListName: ["상품 인수", "상품 이동중", "배송지 도착", "배송 출발", "배송 완료"],
      errorMsg: '',
      isShow: false,  // axios 활성화시 false가 기본 값
      theme: 'default',
      themeColor: {
        "default": {
          "back" : "bg-[#58b5f6]", "hover" : "hover:bg-[lightblue]",
          "active" : "bg-[#58b5f6]", "text" : "text-white" 
        },
        "violet":{
          "back" : "bg-[#4b168e]", "hover": "hover:bg-violet-400",
          "active" : "bg-violet-500", "text" : "text-white" 
        }
      },
      Homepage: "http://st.sweettracker.co.kr/#/"
      }
  },
  
  
  // /api/v1/companylist
  
  //   // axios 환경에서 개발하면 통신 횟수에 제한이 있으므로 로컬(json)환경에서 개발 후 axios로 전환.
  //   // 원본주소, http에 s붙이고 가져올 api목록 주소를 붙이기
  //   // compaynylist뒤에 ?_붙이고 변수명=api키 입력 후 주소를 브라우저에 입력
  created(){
    axios.get("https://info.sweettracker.co.kr/api/v1/companylist?t_key=YohzixCIJ1BU3dAsaesNiA")
    .then((res)=>{
      this.Carriers = res.data.Company
      console.log(this.Carriers)
    })
    .catch((error)=>{
      console.log(error)
    })
  },
   methods:{
    toTop:function(){
      // 첫 번째 방법 - 함수에 Id(top)를 넣어서 원하는 스크롤 값 연결
      // document.getElementById('top').scrollTo(0,0)
      // document.body.scrollTop = 0
      // window.scrollTo({
      //   top:0,  
      //   behavior:'smooth'
      // })
      // console.log('가즈아!!!')

      // 두 번째 방법 - 상수 선언, 지정한 레퍼런스 값의 위치를 찾아감, 대부분의 브라우저 지원
      const rect = this.$refs.home.getBoundingClientRect();
      // console.log(rect)
      // 스크롤에 음수 값을 줘서 페이지 상단에 여백 넣기
      const offset = rect.top + window.scrollY;
      window.scrollTo({
        top: offset,
        behavior: 'smooth'
      })
    },
    bindNumber(){
      //  methods가 아닌 watch로 사용 시 v-model의 값을 넣어야함
      return this.t_invoice = this.t_invoice.replace(/[^0-9]/g, '')
    },
    PostList(){
      // 포스트 리스트가 실행되기전 조건문을 실행
      // 운송장 번호가 비어있다면 에러메세지 출력
      // 운송장번호에 커서를 맞춰줌
      // .focus() - 커서를 맞추며 하나만 사용 가능
      // 예) 아이디와 비밀번호 입력칸이 있다면 둘다 focus를 사용할 수 없다.
      if(this.t_invoice === ''){
        this.errorMsg = "운송장 번호를 입력하세요!";
        // 자바스크립트 코드 - 운송장 번호 미 입력시 커서 반짝이게
        document.querySelector("#t_invoice").focus()
        return
      }
  //      api/v1/trackingInfo 번호 조회
       axios.get("https://info.sweettracker.co.kr/api/v1/trackingInfo", {
         params:{
           t_code: this.t_code,
           t_invoice: this.t_invoice,
           t_key: this.t_key,
         }
       }).then((res)=>{
         console.log(res)
         if(res.data.code === '104'){
           this.errorMsg = res.data.msg
         }else{
           this.Trackings = res.data
           // 에러 메세지 뒤에 = ''를 안써도되지만 혹시모를 버그 방지를 위해
           this.errorMsg = ''
           // 데이터가 있을 때 보여주기
           this.isShow = true
         }
       }).catch((error)=>{
         console.log(error)
       })
    },
   },
  watch:{
    // methods의 bintNumber참고
    t_invoice(){
      return this.t_invoice = this.t_invoice.replace(/[^0-9]/g, '')
    }
  },
  computed:{
    TrackingCode(){
      return this.Carriers.filter((e)=>{
        return e.Code === this.t_code
      }).map(e => e.Name);
    },
    
    // 국내, 국외 택배사 필터 코드
    Company(){
      return this.Carriers.filter((data)=>{
        if(this.isBtn == 1){
          // =과 == 구분 조심
          return data.International == 'false'
        }else{
          return data.International == 'true'
        }
      })
    }
  },
  // components: {
   
  // },
  mounted(){
    console.log(this.Trackings)
    console.log(this.$refs.home.getBoundingClientRect())
  }
}
</script>

