import { createWebHistory, createRouter } from "vue-router";

const routes = [
  {
  path:"/",
  name: "App",
  component: ()=> import('@/App')
  },
  {
    path:"/TrackingPage",
    name: "TrackingPage",
    component: ()=> import('@/pages/TrackingPage')
  },
  // {
  //   path:"/aa",
  //   name: "TrackingPage",
  //   component: ()=> import('@/pages/aa')
  // }
]

const router = createRouter({
  history: createWebHistory(),
  routes
})
export default router;